import { ChangeDetectorRef, Component, Directive, Input, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { HttpErrorResponse } from '@angular/common/http';
import {
  catchError,
  concatMap,
  finalize,
  first,
  map,
  switchMap,
  takeUntil,
  tap,
  throttleTime,
  withLatestFrom,
} from 'rxjs/operators';
import { BehaviorSubject, combineLatest, EMPTY, merge, Observable, of } from 'rxjs';
import { BbTemplate } from '@backbase/foundation-ang/core';
import {
  AbstractBaseMessagesConversationThreadService,
  BaseConversationThreadComponent,
  BaseUploadAttachmentsService,
  getPropertyValue,
  ManipulationType,
  MessagesClientUploadAttachmentsService,
  MessagesEncodingService,
  MessagesManipulationConfirmModalService,
  MessagesUploadAttachmentsService,
  ReadUnreadStatusType,
  SharedMethodsService,
  shareReplayOnce,
} from '@backbase/messages-common';
import { DeleteDraftsResponseBody, MessageThreadGetResponseBody } from '@backbase/messages-v5-http-ang';
import { MessagesConversationThreadWidgetService } from './services/messages-conversation-thread-widget-ang.service';
import { WidgetPropertiesService } from './services/widget-properties.service';
import { MessagesConversationThreadWidgetContext } from './model/conversation';

@Component({
  selector: 'bb-messages-conversation-thread-widget-ang',
  templateUrl: './messages-conversation-thread-widget-ang.component.html',
  providers: [
    MessagesEncodingService,
    WidgetPropertiesService,
    MessagesManipulationConfirmModalService,
    MessagesUploadAttachmentsService,
    {
      provide: BaseUploadAttachmentsService,
      useClass: MessagesClientUploadAttachmentsService,
    },
    {
      provide: AbstractBaseMessagesConversationThreadService,
      useClass: MessagesConversationThreadWidgetService,
    },
  ],
})
export class MessagesConversationThreadWidgetComponent extends BaseConversationThreadComponent implements OnInit {
  readonly hostRef = this;

  /**
   * The stream with the mailbox type.
   */
  @Input('mailboxTypeTitle') set mailboxTypeTitleValue(value: string) {
    this.mailboxTypeTitle$$.next(value);
  }

  /**
   * The stream with the maximum attachment size.
   */
  @Input('maxAttachmentSize') set maxAttachmentSizeValue(value: string | number) {
    this.maxAttachmentSize$$.next(String(value));
  }

  /**
   * The stream with the maximum message length.
   */
  @Input('replyMessageMaxLength') set replyMessageMaxLengthValue(value: string | number) {
    this.replyMessageMaxLength$$.next(String(value));
  }

  /**
   * The stream with the customer service title.
   */
  @Input('customerServiceTitle') set customerServiceTitleValue(value: string) {
    this.customerServiceTitle$$.next(value);
  }

  private readonly replyStream: Observable<DeleteDraftsResponseBody | undefined> = this.getReplyStream();

  /**
   * The flag indicating if the remove confirmation modal is opened or closed.
   */
  removeModalOpened = false;

  private readonly mailboxTypeTitle$$ = new BehaviorSubject<string | undefined>(undefined);
  private readonly maxAttachmentSize$$ = new BehaviorSubject<string | undefined>(undefined);
  private readonly replyMessageMaxLength$$ = new BehaviorSubject<string | undefined>(undefined);
  private readonly customerServiceTitle$$ = new BehaviorSubject<string | undefined>(undefined);

  /**
   * The stream with the conversation data.
   */
  conversation: Observable<MessageThreadGetResponseBody> = this.getConversationStream();

  /**
   * The stream with the mailbox type.
   */
  readonly mailboxTypeTitle: Observable<string> = getPropertyValue(
    this.mailboxTypeTitle$$,
    this.properties.mailboxTypeTitle,
  );

  /**
   * The stream with the maximum attachment size.
   */
  readonly maxAttachmentSize: Observable<string> = getPropertyValue(
    this.maxAttachmentSize$$,
    this.properties.maxAttachmentSize,
  );

  /**
   * The stream with the maximum message length.
   */
  readonly replyMessageMaxLength: Observable<string> = getPropertyValue(
    this.replyMessageMaxLength$$,
    this.properties.replyMessageMaxLength,
  );

  /**
   * The stream with the maximum message length number.
   */
  readonly replyMessageMaxLength$: Observable<number> = getPropertyValue(
    this.replyMessageMaxLength$$,
    this.properties.replyMessageMaxLength,
  ).pipe(map(val => Number(val)));

  /**
   * The stream with the customer service title.
   */
  readonly customerServiceTitle$: Observable<string> = getPropertyValue(
    this.customerServiceTitle$$,
    this.properties.customerServiceTitle,
  );

  constructor(
    private readonly messagesService: MessagesConversationThreadWidgetService,
    private readonly properties: WidgetPropertiesService,
    protected readonly encodingService: MessagesEncodingService,
    protected readonly uploadService: MessagesUploadAttachmentsService,
    readonly confirmModalService: MessagesManipulationConfirmModalService,
    protected readonly apiThreadService: AbstractBaseMessagesConversationThreadService,
    protected readonly activatedRoute: ActivatedRoute,
    protected readonly sharedService: SharedMethodsService,
    private readonly cd: ChangeDetectorRef,
  ) {
    super(apiThreadService, activatedRoute, sharedService, confirmModalService, uploadService, encodingService);
  }

  ngOnInit() {
    this.conversation.pipe(first(), takeUntil(this.unsubscribe)).subscribe(() => {
      this.initSubscriptions();
    });
  }

  private getReplyStream(): Observable<DeleteDraftsResponseBody | undefined> {
    return this.replyPressed.pipe(
      throttleTime(1000),
      withLatestFrom(this.conversationId),
      concatMap(([obj, conversationId]) => {
        this.formLoading = true;

        return this.messagesService.postReplyMessage(conversationId, obj).pipe(
          switchMap(() =>
            this.draftResponse ? this.messagesService.deleteDraft(this.draftResponse.id) : of(undefined),
          ),
          tap(() => {
            this.uploadService.reset();
            this.replyFormGroup.reset();
            this.sharedService.openNotification(this.replySuccessTpl);
          }),
          catchError(error => {
            this.sharedService.openNotification(this.sharedService.getErrorMessage(error), 'error');
            this.cd.markForCheck();

            return EMPTY;
          }),
          finalize(() => {
            this.formLoading = false;
          }),
        );
      }),
    );
  }

  private getConversationStream(): Observable<MessageThreadGetResponseBody> {
    return combineLatest([this.conversationId, merge(this.replyStream, of(undefined))]).pipe(
      tap(() => {
        this.loading = true;
        this.errorResponse = undefined;
      }),
      switchMap(([conversationId]) =>
        this.messagesService.getConversationThread(conversationId).pipe(
          withLatestFrom(this.customerServiceTitle$),
          map(([conversation, customerName]) => {
            const messages =
              conversation &&
              conversation.messages
                .map(message => ({
                  ...message,
                  body: this.encodingService.b64Decode(message.body),
                  attachments: message.attachments && this.apiThreadService.attachmentsMapper(message.attachments),
                  sender: {
                    ...message.sender,
                    name: message.sender.id === 'customer.service' ? customerName : message.sender.name,
                  },
                }))
                .reverse();

            return {
              ...conversation,
              messages,
            } as MessageThreadGetResponseBody;
          }),
          tap(conversation => {
            this.draftResponse = conversation.draft && { ...conversation.draft };

            if (this.draftResponse) {
              this.toggleReplyForm(true);

              if (this.draftResponse.attachments) {
                this.replyFormGroup.controls['attachments'].setValue([...this.draftResponse.attachments]);
              }

              if (this.draftResponse.body) {
                this.replyFormGroup.controls['body'].setValue(this.encodingService.b64Decode(this.draftResponse.body));
              }
            }
          }),
          catchError((error: HttpErrorResponse) => {
            this.errorResponse = error;
            this.cd.markForCheck();

            return EMPTY;
          }),
          finalize(() => {
            this.loading = false;
          }),
        ),
      ),
      shareReplayOnce(),
      takeUntil(this.unsubscribe),
    );
  }

  /**
   * The method to discard a message or a draft.
   */
  confirmConfirmationModal() {
    switch (this.confirmationModal && this.confirmationModal.type) {
      case ManipulationType.discard:
        this.toggleReplyForm(false);
        break;
      case ManipulationType.discardDraft:
        this.deleteDraft().pipe(takeUntil(this.unsubscribe)).subscribe();

        this.toggleReplyForm(false);
        break;
    }

    this.confirmModalService.toggleModal(false);
  }

  /**
   * The method to open the removal modal.
   */
  openRemoveModal() {
    this.removeModalOpened = true;
  }

  /**
   * The method to close the removal modal.
   */
  closeRemoveModal() {
    this.removeModalOpened = false;
  }

  /**
   * The method to remove a conversation.
   */
  removeConversation() {
    this.closeRemoveModal();
    this.conversationId
      .pipe(
        switchMap(id => this.messagesService.deleteConversation(id)),
        takeUntil(this.unsubscribe),
      )
      .subscribe(
        () => {
          if (this.deleteSuccessTpl) {
            this.sharedService.openNotification(this.deleteSuccessTpl);
          }
          this.navigateBack();
        },
        error => this.sharedService.openNotification(this.sharedService.getErrorMessage(error), 'error'),
      );
  }

  /**
   * The method to toggle the read/unread conversation status.
   *
   * @param status Status type
   */
  toggleRead(status: ReadUnreadStatusType = ReadUnreadStatusType.UNREAD) {
    this.markAsReadUnreadSubject.next(status);
  }

  /**
   * The method to open the discard confirmation modal.
   */
  handleDiscardClick() {
    const manipulationType = this.draftResponse ? this.manipulationTypes.discardDraft : this.manipulationTypes.discard;

    this.confirmModalService.toggleModal(true, manipulationType, this.replyFormGroup);
  }
}

@Directive({
  selector: '[bbMessagesConversationThreadCustomizable]',
})
export class MessagesConversationThreadCustomizableDirective extends BbTemplate<
  MessagesConversationThreadWidgetComponent,
  MessagesConversationThreadWidgetContext
> {}
